import Container from "../components/Container/Container";

export default function Design() {
  return (
    <Container>
      <div className="page">
        <h1>Design</h1>
        <h2>Design</h2>
        <h3>Design</h3>
        <p>Design</p>
        <style jsx>
          {`
            .page:nth-child(1) {
              font-family: "Raleway";
              margin: 0.625em 0;
            }

            @media only screen and (max-width: 1024px) {
              .page:nth-child(1) {
                margin: 1.625em 0;
              }
            }
          `}
        </style>
      </div>
    </Container>
  );
}
